* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}

.App {
  font-family: "Open Sans", sans-serif;
}

h1 {
  color: #381fb4;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  font-size: 50px;
  position: relative;
  z-index: 100;
  padding: 20px;
}

h1,
h2 {
  font-weight: 700;
  background: linear-gradient(120deg, #95ccff 0%, #381fb4 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

button,
.button2 {
  font-family: "Open Sans", sans-serif;
  color: #381fb4;
}

.line {
  width: 100%;
  border-top: 2px solid #b8c0fb;
}

.generalContentContainer {
  padding: 80px 80px;
}

.contactFooterContainer {
  background-image: linear-gradient(to bottom, white, #b8c0fb);
}

.fa-brands {
  color: #381fb4;
}

@media (max-width: 950px) {
  .generalContentContainer {
    padding: 0;
  }
}
.skillCardContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.skillCard {
  width: 400px;
  height: 450px;
  padding: 30px 20px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 8px 8px 10px rgba(169, 169, 170, 0.4666666667), inset 8px 8px 10px rgba(255, 255, 255, 0.4941176471);
  gap: 30px;
  margin-bottom: 50px;
}

.skillIllustration {
  height: 150px;
  width: 150px;
  background: #f3f3f3;
  border-radius: 30px;
  box-shadow: -8px -8px 8px rgba(255, 255, 255, 0.4784313725), 8px 8px 8px rgba(169, 169, 170, 0.4784313725);
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(120deg, #95ccff 0%, #381fb4 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.skillTitle {
  font-family: "Open Sans", sans-serif;
  width: 100%;
  text-align: center;
}

.skillsList {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  padding-left: 0;
}

.skill {
  padding: 5px;
  box-shadow: -8px -8px 8px rgba(255, 255, 255, 0.4784313725), 8px 8px 8px rgba(169, 169, 170, 0.4784313725);
  border-radius: 10px;
  transition: transform 0.2s;
  width: 90px;
  text-align: center;
}

.skill:hover {
  transform: scale(1.15);
}

@media (max-width: 951px) {
  .skillCardContainer {
    padding: 100px;
  }
}
@media (max-width: 700px) {
  .skillCardContainer {
    padding: 0 20px 100px 20px;
    gap: 0;
  }
  .skillCard {
    width: 100%;
  }
}/*# sourceMappingURL=SkillCard.css.map */