$main-color: #381fb4;
$lightMain-color: #95ccff;
$secondary-color: #b8c0fb;
$lightSecondary-color: #dee1fa;
$third-color: #f2f2f2;
$white-color: white;

$title-color: #b8c0fb;

$font-color: black;
$accentuedFont-color: #381fb4;
$fadedFont-color: #b8c0fb;
$softFont-color: white;

$main-font: "Open Sans", sans-serif;
$accentuation-font: "Open Sans", sans-serif;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}

.App {
  font-family: $main-font;
}

h1 {
  color: $main-color;
  font-family: $accentuation-font;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  font-size: 50px;
  position: relative;
  z-index: 100;
  padding: 20px;
}

h1,
h2 {
  font-weight: 700;
  background: linear-gradient(120deg, $lightMain-color 0%, $main-color 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

button,
.button2 {
  font-family: $accentuation-font;
  color: $main-color;
}

.line {
  width: 100%;
  border-top: 2px solid $title-color;
}

.generalContentContainer {
  padding: 80px 80px;
}

.contactFooterContainer {
  background-image: linear-gradient(to bottom, white, $secondary-color);
}

.fa-brands {
  color: $main-color;
}

@media (max-width: 950px) {
  .generalContentContainer {
    padding: 0;
  }
}
