* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}

.App {
  font-family: "Open Sans", sans-serif;
}

h1 {
  color: #381fb4;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
  font-size: 50px;
  position: relative;
  z-index: 100;
  padding: 20px;
}

h1,
h2 {
  font-weight: 700;
  background: linear-gradient(120deg, #95ccff 0%, #381fb4 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

button,
.button2 {
  font-family: "Open Sans", sans-serif;
  color: #381fb4;
}

.line {
  width: 100%;
  border-top: 2px solid #b8c0fb;
}

.generalContentContainer {
  padding: 80px 80px;
}

.contactFooterContainer {
  background-image: linear-gradient(to bottom, white, #b8c0fb);
}

.fa-brands {
  color: #381fb4;
}

@media (max-width: 950px) {
  .generalContentContainer {
    padding: 0;
  }
}
.footerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 50px;
}

.linksFooterContainer {
  display: flex;
  flex-direction: row;
  gap: 4vw;
}

.footerText,
.footerLinks {
  font-family: "Open Sans", sans-serif;
  background: linear-gradient(120deg, #95ccff 0%, #381fb4 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-decoration: none;
  font-size: 2vh;
  height: 5vh;
  padding: 1vh 0;
}

.footerLinks:hover {
  color: #381fb4;
}

.footerText {
  font-weight: 700;
}

@media (max-width: 1000px) {
  .footerContainer {
    flex-direction: column;
    align-items: center;
  }
  .footerText {
    font-size: 20px;
  }
}/*# sourceMappingURL=Footer.css.map */